import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';
import ExternalLink from '@common/ExternalLink';

import { ReactComponent as AirbnbLogo } from '@images/logos/airbnb.svg';
import { ReactComponent as AppleMusicLogo } from '@images/logos/apple-music.svg';
import { ReactComponent as CokeLogo } from '@images/logos/coca-cola.svg';
import { ReactComponent as NodeLogo } from '@images/logos/nodejs.svg';
import { ReactComponent as NikeLogo } from '@images/logos/nike.svg';
import { ReactComponent as InstagramLogo } from '@images/logos/instagram.svg';

const LOGOS = [
  {
    logo: AirbnbLogo,
    link: 'https://airbnb.io'
  },
  {
    logo: AppleMusicLogo,
    link: 'https://www.apple.com/in/music/'
  },
  {
    logo: CokeLogo,
    link: 'https://coca-cola.com'
  },
  {
    logo: NodeLogo,
    link: 'https://nodejs.org'
  },
  {
    logo: NikeLogo,
    link: 'https://nike.com'
  },
  {
    logo: InstagramLogo,
    link: 'https://instagram.com'
  }
];

const UsedBy = () => (
  <StaticQuery
    query={graphql`
      query {
        art_story: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "241" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="brands" accent>
        <StyledContainer>
          <LogoGrid>
            <h1>Use Cases</h1>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua.
              Aliquam purus sit amet luctus venenatis lectus magna fringilla. Ac
              odio tempor orci dapibus ultrices in iaculis. Ultrices in iaculis
              nunc sed augue lacus viverra. Mollis nunc sed id semper risus in
              hendrerit gravida. Cras pulvinar mattis nunc sed blandit libero.
              Justo donec enim diam vulputate ut pharetra sit. Gravida dictum
              fusce ut placerat orci. Ipsum nunc aliquet bibendum enim facilisis
              gravida neque convallis.
            </p>
          </LogoGrid>
          <Art>
            <Img fluid={data.art_story.childImageSharp.fluid} />
          </Art>
        </StyledContainer>
      </Section>
    )}
  />
);



const StyledContainer = styled(Container)`
  display: flex;
  justify-content: center;

  @media (max-width: ${props => props.theme.screen.md}) {
    align-items: center;
    flex-direction: column;
  }
`;

const LogoGrid = styled.div`
  flex: 0 0 50%;
  padding: 20px;

  @media (max-width: ${props => props.theme.screen.md}) {
    flex: 0 0 auto;
  }
`;

const Art = styled.figure`
  flex: 0 0 50%;
  order: -1;
  width: 100%;

  @media (max-width: ${props => props.theme.screen.md}) {
    flex: 0 0 auto;
    order: 1;
  }
`;

export default UsedBy;

