import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';

const TEAM = [
  {
    name: 'Josh Peck',
    image: 'josh.jpg',
    role: 'Founder'
  },
  {
    name: 'Lisa Haydon',
    image: 'lisa.jpg',
    role: 'Art Director'
  },
  {
    name: 'Ashlyn Harris',
    image: 'ashlyn.jpg',
    role: 'Frontend Engineer'
  },
  {
    name: 'Todd Joseph',
    image: 'todd.jpg',
    role: 'Designer'
  },
  {
    name: 'Martin White',
    image: 'martin.jpg',
    role: 'Backend Engineer'
  },
  {
    name: 'Rose Leslie',
    image: 'rose.jpg',
    role: 'Marketing'
  }
];

const Team = () => (
  <StaticQuery
    query={graphql`
      query {
        allFile(filter: { sourceInstanceName: { eq: "team" } }) {
          edges {
            node {
              relativePath
              childImageSharp {
                fluid(maxWidth: 400, maxHeight: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
        art_team: file(sourceInstanceName: { eq: "art" }, name: { eq: "243" }) {
          childImageSharp {
            fluid(maxWidth: 1600) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="team" accent="secondary">
        <Container style={{ position: 'relative' }}>
          <TeamGrid>
            <h1>Features</h1>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua.
              Aliquam purus sit amet luctus venenatis lectus magna fringilla. Ac
              odio tempor orci dapibus ultrices in iaculis. Ultrices in iaculis
              nunc sed augue lacus viverra. Mollis nunc sed id semper risus in
              hendrerit gravida. Cras pulvinar mattis nunc sed blandit libero.
              Justo donec enim diam vulputate ut pharetra sit. Gravida dictum
              fusce ut placerat orci. Ipsum nunc aliquet bibendum enim facilisis
              gravida neque convallis. Et molestie ac feugiat sed lectus. Turpis
              egestas integer eget aliquet nibh. Feugiat vivamus at augue eget
              arcu dictum varius. Nisi porta lorem mollis aliquam ut. Adipiscing
              at in tellus integer. Pellentesque elit ullamcorper dignissim cras
              tincidunt.
            </p>
          </TeamGrid>
          <Art>
            <Img fluid={data.art_team.childImageSharp.fluid} />
          </Art>
          <ArtMobile>
            <Img fluid={data.art_team.childImageSharp.fluid} />
          </ArtMobile>
        </Container>
      </Section>
    )}
  />
);

const TeamGrid = styled.div`
  width: 65%;

  @media (max-width: ${props => props.theme.screen.md}) {
    width: 100%;
  }
`;

const Art = styled.figure`
  width: 800px;
  margin: -80px 0;
  position: absolute;
  top: 0;
  left: 70%;

  @media (max-width: ${props => props.theme.screen.lg}) {
    top: 20%;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    display: none;
  }
`;

const ArtMobile = styled.figure`
  width: 100%;
  margin: 0;
  display: none;
  margin-top: 64px;
  margin-bottom: -60%;

  @media (max-width: ${props => props.theme.screen.md}) {
    display: block;
  }
`;

const Title = styled.p`
  margin-top: 16px;
  color: ${props => props.theme.color.black.regular};
`;

const Subtitle = styled.p`
  ${props => props.theme.font_size.small};
  color: ${props => props.theme.color.black.light};
`;

export default Team;
